@use '../utils/' as u;

.menu-btn {
    position: absolute;
    z-index: 10;
    right: 1rem;
    top: 1rem;
    height: 20px;
    width: 28px;
    cursor: pointer;
    @include u.transition-ease;

    @include u.breakpoint-up(md) {
        visibility: hidden;
    }

    &__burger {
        position: absolute;
        right: 0;
        top: 0.5rem;
        width: 28px;
        height: 3px;
        background-color: hsl(var(--clr-base-white));
        @include u.transition-ease;

        &::before {
            content: '';
            position: absolute;
            top: -8px;
            width: 28px;
            height: 3px;
            background-color: hsl(var(--clr-base-white));
            @include u.transition-ease;
        }

        &::after {
            content: '';
            position: absolute;
            top: 8px;
            width: 20px;
            height: 3px;
            background-color: hsl(var(--clr-base-white));
            @include u.transition-ease;
        }

        &.open {
            transform: rotate(720deg);
            background-color: transparent;

            &::before {
                transform: rotate(45deg) translate(5px, 8px);
            }

            &::after {
                width: 28px;
                transform: rotate(-45deg) translate(3px, -7px);
            }
        }
    }
}
