@use '../utils' as u;

.boxes {
    display: none;
    
    .box {
        @include u.flex-center;
        width: 8em;
        height: 8em;
        border: 2px solid hsl(var(--clr-base-white));
        background-color: hsla(var(--clr-base-white), .1);
        border-radius: .5em;
        @include u.transition-ease;
    
        &:hover {
            transform: rotateY(360deg);
        }
    }

    @include u.breakpoint-up(md) {
        display: block;
    }
}