@use '../utils/' as u;

.home {
    color: hsl(var(--clr-base-white));
    background-color: hsla(var(--clr-base-black), .5);
    background-image: url('../images/fotis-fotopoulos-6sAl6aQ4OWI-unsplash.jpg'),linear-gradient(to right, hsla(var(--clr-base-black), .5) 40%, hsl(var(--clr-base-white)));
    background-size: cover;
    background-position: center;
    background-blend-mode: multiply;

    .header-container {
        @include u.container;
        display: flex;
        align-items: flex-start;
        justify-content: space-between;
    }

    .hero__container {
        color: white;
        text-align: center;
        padding: 5em 0;

        .intro {
            &__name {
                margin-bottom: .5em;
            }
            &__job-desc {
                font-size: var(--fs-h4);
                font-weight: var(--fw-regular);
                margin-bottom: 2.5em;
            }
        }

        .dl-btn {
            position: relative;
            font-family: var(--ff-inter);
            font-size: var(--fs-small);
            font-weight: var(--fw-medium);
            text-transform: uppercase;
            color: hsl(var(--clr-base-white));
            background-color: hsla(var(--clr-base-white), .25);
            border: 1px solid hsl(var(--clr-base-white));
            padding: .75em 2em;
            z-index: 1;
            cursor: pointer;

            &::before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                right: 0;
                z-index: -1;
                background-color: hsl(var(--clr-accent));
                transform: scale(0, 1);
                @include u.transition-ease;
            }

            &:hover {
                color: hsl(var(--clr-base-black));
                
                &::before {
                    transform: scale(1, 1);
                }
            }
        }
    }
}