@use '../utils/' as u;

.nav {
    position: absolute;
    top: 0;
    right: 0;
    width: 20em;
    height: auto;
    overflow: hidden;
    background-color: hsla(var(--clr-base-black), .9);
    transform: translateY(-100%);
    @include u.transition-ease;
    z-index: 5;

    .nav-list {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height: 100vh;
        margin: 0;
        padding: 0;

        &__item {
            list-style-type: none;
            margin: .5em;
            transform: translateX(100vw);
            @include u.transition-ease;
        }

        &__link {
            font-size: var(--fs-h4);
            padding: .325em .5em;
            color: hsl(var(--clr-base-white));
            @include u.transition-ease;

            &::after {
                content: '';
                position: absolute;
                width: 100%;
                height: 0.1em;
                left: 0;
                bottom: 0;
                background-color: hsl(var(--clr-accent));
                transform: scale(0, 1);
                transform-origin: 0% 100%;
                @include u.transition-ease;
            }

            &:hover, &:focus {
                color: hsl(var(--clr-accent));
                
                &::after {
                    transform: scale(1, 1);
                }
            }
        }
    }

    &.open {
        transform: translateY(0);

        .nav-list {
            &__item {
                transform: translateX(0);
            }

            &__link::after {
                width: 150%;
                left: -25%;
                transform-origin: 50% 50%;
            }
        }
    }

    @for $i from 1 through 7 {
        .nav-list__item:nth-child(#{$i}) {
            transition-delay: ($i * 0.05s) + 0.15s;
        }
    }

    @include u.breakpoint-up(md) {
        transform: translateY(0);
        width: auto;
        background-color: transparent;
        margin: .5em 1em;

        .nav-list {
            flex-direction: row;
            height: auto;

            &__item {
                transform: translateX(0);
            }

            &__link {
                font-size: var(--fs-body);
            }
        }
    }
}