:root {
  --ff-inter: "Inter", sans-serif;
  --fs-h1: 2rem;
  --fs-h2: 1.75rem;
  --fs-h3: 1.5rem;
  --fs-h4: 1.25rem;
  --fs-body: 1rem;
  --fs-small: .75rem;
  --fw-bold: 700;
  --fw-medium: 500;
  --fw-regular: 400;
}

@media (min-width: 960px) {
  :root {
    --fs-h1: 2.125rem;
    --fs-h2: 1.875rem;
    --fs-h3: 1.625rem;
    --fs-h4: 1.325rem;
    --fs-body: 1.125rem;
    --fs-small: .875rem ;
  }
}
:root {
  --clr-light: 0, 100%, 98%;
  --clr-dark: 0, 0%, 0%;
  --clr-primary: 188, 72%, 36%;
  --clr-accent: 72, 66%, 51%;
  --clr-base-black: 0, 0%, 0%;
  --clr-base-white: 0, 0%, 100%;
}

*, *::before, *::after {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: var(--ff-inter);
  font-size: var(--fs-body);
  font-weight: var(--fw-regular);
  color: hsl(var(--clr-base-black));
  background-color: hsl(var(--clr-base-white));
  line-height: 1.6;
}

img {
  display: block;
  max-width: 100%;
}

h1, h2, h3, h4 {
  margin-top: 0;
  font-weight: var(--fw-bold);
}

h1 {
  font-size: var(--fs-h1);
}

h2 {
  font-size: var(--fs-h2);
}

h3 {
  font-size: var(--fs-h3);
}

h4 {
  font-size: var(--fs-h4);
}

p {
  margin-top: 0;
}

a {
  text-decoration: none;
}

.logo__name {
  font-size: var(--fs-h1);
  font-weight: var(--fw-medium);
  margin-bottom: 0;
}

.nav {
  position: absolute;
  top: 0;
  right: 0;
  width: 20em;
  height: auto;
  overflow: hidden;
  background-color: hsla(var(--clr-base-black), 0.9);
  transform: translateY(-100%);
  transition: all 400ms ease-in-out;
  z-index: 5;
}
.nav .nav-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  padding: 0;
}
.nav .nav-list__item {
  list-style-type: none;
  margin: 0.5em;
  transform: translateX(100vw);
  transition: all 400ms ease-in-out;
}
.nav .nav-list__link {
  font-size: var(--fs-h4);
  padding: 0.325em 0.5em;
  color: hsl(var(--clr-base-white));
  transition: all 400ms ease-in-out;
}
.nav .nav-list__link::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.1em;
  left: 0;
  bottom: 0;
  background-color: hsl(var(--clr-accent));
  transform: scale(0, 1);
  transform-origin: 0% 100%;
  transition: all 400ms ease-in-out;
}
.nav .nav-list__link:hover, .nav .nav-list__link:focus {
  color: hsl(var(--clr-accent));
}
.nav .nav-list__link:hover::after, .nav .nav-list__link:focus::after {
  transform: scale(1, 1);
}
.nav.open {
  transform: translateY(0);
}
.nav.open .nav-list__item {
  transform: translateX(0);
}
.nav.open .nav-list__link::after {
  width: 150%;
  left: -25%;
  transform-origin: 50% 50%;
}
.nav .nav-list__item:nth-child(1) {
  transition-delay: 0.2s;
}
.nav .nav-list__item:nth-child(2) {
  transition-delay: 0.25s;
}
.nav .nav-list__item:nth-child(3) {
  transition-delay: 0.3s;
}
.nav .nav-list__item:nth-child(4) {
  transition-delay: 0.35s;
}
.nav .nav-list__item:nth-child(5) {
  transition-delay: 0.4s;
}
.nav .nav-list__item:nth-child(6) {
  transition-delay: 0.45s;
}
.nav .nav-list__item:nth-child(7) {
  transition-delay: 0.5s;
}
@media (min-width: 720px) {
  .nav {
    transform: translateY(0);
    width: auto;
    background-color: transparent;
    margin: 0.5em 1em;
  }
  .nav .nav-list {
    flex-direction: row;
    height: auto;
  }
  .nav .nav-list__item {
    transform: translateX(0);
  }
  .nav .nav-list__link {
    font-size: var(--fs-body);
  }
}

.menu-btn {
  position: absolute;
  z-index: 10;
  right: 1rem;
  top: 1rem;
  height: 20px;
  width: 28px;
  cursor: pointer;
  transition: all 400ms ease-in-out;
}
@media (min-width: 720px) {
  .menu-btn {
    visibility: hidden;
  }
}
.menu-btn__burger {
  position: absolute;
  right: 0;
  top: 0.5rem;
  width: 28px;
  height: 3px;
  background-color: hsl(var(--clr-base-white));
  transition: all 400ms ease-in-out;
}
.menu-btn__burger::before {
  content: "";
  position: absolute;
  top: -8px;
  width: 28px;
  height: 3px;
  background-color: hsl(var(--clr-base-white));
  transition: all 400ms ease-in-out;
}
.menu-btn__burger::after {
  content: "";
  position: absolute;
  top: 8px;
  width: 20px;
  height: 3px;
  background-color: hsl(var(--clr-base-white));
  transition: all 400ms ease-in-out;
}
.menu-btn__burger.open {
  transform: rotate(720deg);
  background-color: transparent;
}
.menu-btn__burger.open::before {
  transform: rotate(45deg) translate(5px, 8px);
}
.menu-btn__burger.open::after {
  width: 28px;
  transform: rotate(-45deg) translate(3px, -7px);
}

.boxes {
  display: none;
}
.boxes .box {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 8em;
  height: 8em;
  border: 2px solid hsl(var(--clr-base-white));
  background-color: hsla(var(--clr-base-white), 0.1);
  border-radius: 0.5em;
  transition: all 400ms ease-in-out;
}
.boxes .box:hover {
  transform: rotateY(360deg);
}
@media (min-width: 720px) {
  .boxes {
    display: block;
  }
}

.home {
  color: hsl(var(--clr-base-white));
  background-color: hsla(var(--clr-base-black), 0.5);
  background-image: url("../images/fotis-fotopoulos-6sAl6aQ4OWI-unsplash.jpg"), linear-gradient(to right, hsla(var(--clr-base-black), 0.5) 40%, hsl(var(--clr-base-white)));
  background-size: cover;
  background-position: center;
  background-blend-mode: multiply;
}
.home .header-container {
  width: min(95%, 1200px);
  margin: 0 auto;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.home .hero__container {
  color: white;
  text-align: center;
  padding: 5em 0;
}
.home .hero__container .intro__name {
  margin-bottom: 0.5em;
}
.home .hero__container .intro__job-desc {
  font-size: var(--fs-h4);
  font-weight: var(--fw-regular);
  margin-bottom: 2.5em;
}
.home .hero__container .dl-btn {
  position: relative;
  font-family: var(--ff-inter);
  font-size: var(--fs-small);
  font-weight: var(--fw-medium);
  text-transform: uppercase;
  color: hsl(var(--clr-base-white));
  background-color: hsla(var(--clr-base-white), 0.25);
  border: 1px solid hsl(var(--clr-base-white));
  padding: 0.75em 2em;
  z-index: 1;
  cursor: pointer;
}
.home .hero__container .dl-btn::before {
  content: "";
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: -1;
  background-color: hsl(var(--clr-accent));
  transform: scale(0, 1);
  transition: all 400ms ease-in-out;
}
.home .hero__container .dl-btn:hover {
  color: hsl(var(--clr-base-black));
}
.home .hero__container .dl-btn:hover::before {
  transform: scale(1, 1);
}/*# sourceMappingURL=style.css.map */